import { GroupBase, StylesConfig } from "react-select";
import styled, { CSSObject } from "styled-components";

import { Icon } from "../Icon/Icon";
import { Spinner } from "../Spinner";
import {
	colors,
	displayFontSizes, marginsCss, spacingMap,
} from "../styleguide";
import Typography from "../Typography/Typography";
import { pxToRem } from "../utils";

import { ComboboxValidation, OptionType } from "./Combobox.types";

export const comboboxClassNamePrefix = "Combobox";

export const sharedCss: CSSObject = {
	fontFamily: displayFontSizes.bodyCopy.$fontFamily,
	fontWeight: displayFontSizes.bodyCopy.$fontWeight,
	fontSize: displayFontSizes.bodyCopy.$fontSize,
	lineHeight: displayFontSizes.bodyCopy.lineHeight,
};

export const validationColor: Record<ComboboxValidation, string> = {
	valid: colors.green,
	invalid: colors.destructive,
};

export const createStyles = <
	Option extends OptionType,
	IsMulti extends boolean,
	Group extends GroupBase<Option> = GroupBase<Option>,
>(styles?: StylesConfig<Option, IsMulti, Group>, validation?: ComboboxValidation):
StylesConfig<Option, IsMulti, Group> => ({
		// allow some styles to be provided like for the menuPortal if the zIndex needs to be changed
		// otherwise apply our custom styles
		...styles,
		control: (provided, {
			isFocused, isDisabled, selectProps, innerProps,
		}) => ({
			...provided,
			minHeight: "40px",
			paddingLeft: "0.75rem",
			paddingRight: "0.75rem",
			borderWidth: "1px",
			borderStyle: "solid",
			borderRadius: "8px",
			color: validation ? validationColor[validation] : colors.defaultGray,
			borderColor: validation === "invalid" ? colors.destructive : colors.defaultGray,
			":hover": {
				borderColor: colors.black,
				color: validation ? validationColor[validation] : colors.black,
			},

			...(isFocused && {
				boxShadow: `0 0 0 4px ${colors.midBlue}40`,
				borderColor: validation === "invalid" ? colors.destructive : colors.midBlue,
				color: validation ? validationColor[validation] : colors.black,
				":hover": {
					borderColor: validation === "invalid" ? colors.destructive : colors.midBlue,
				},
			}),

			...(isDisabled && {
				background: colors.lightestGray,
				borderColor: colors.defaultGray,
				color: innerProps.defaultValue || !selectProps.placeholder
					? `${colors.black}A6`
					: `${colors.black}4D`,
			}),
		}),
		placeholder: (provided) => ({
			...provided,
			...sharedCss,
			color: colors.defaultGray,
			margin: 0,
		}),
		input: (provided) => ({
			...provided,
			...sharedCss,
			color: colors.black,
			margin: 0,
			padding: 0,
			cursor: "text",
		}),
		valueContainer: (provided, { selectProps: { comboboxIcon, comboboxLeftIcon } }) => ({
			...provided,
			...sharedCss,
			padding: (comboboxIcon || comboboxLeftIcon) ? `${pxToRem(10)} ${spacingMap.xs}` : `${pxToRem(10)} 0`,
			color: colors.black,
			gap: spacingMap.xs,
		}),
		menu: (provided, props) => ({
			...provided,
			marginTop: spacingMap.xxs,
			boxShadow: "0 16px 32px -8px rgba(0, 0, 0, 0.08),"
			+ "0 8px 8px rgba(0, 0, 0, 0.08),"
			+ "0 4px 4px rgba(0, 0, 0, 0.08),"
			+ "0 2px 2px rgba(0, 0, 0, 0.08)",
			borderRadius: "8px",
			...(styles?.menu?.(provided, props)),
		}),
		menuList: (provided) => ({
			...provided,
			padding: spacingMap.xs,
			gap: spacingMap.xs,
			display: "flex",
			flexDirection: "column",
		}),
		option: (provided, props) => ({
			...provided,
			...sharedCss,
			minHeight: "40px",
			display: "flex",
			alignItems: "center",
			justifyContent: "space-between",
			border: "2px solid transparent",

			...(props.isFocused && {
				border: `2px solid ${colors.midBlue}`,
				borderRadius: "8px",
				cursor: "pointer",
			}),

			...(props.isSelected && {
				color: colors.midBlue,
				background: "none",
			}),

			":active": {
				background: "none",
			},
			...(styles?.option?.(provided, props)),
		}),
		multiValueLabel: () => ({}),
		multiValue: () => ({}),
		group: (provided) => ({
			...provided,
			padding: 0,
		}),
		groupHeading: (provided) => ({
			...provided,
			margin: 0,
		}),
	});

export const ComboboxWrapper = styled.div`
	display: flex;
	flex-direction: column;

	${marginsCss};
`;

export const NoResultsFound = styled(Typography)`
	align-self: center;
`;

export const LoadingIndicator = styled(Spinner)`
	align-self: center;
`;

export const OptionContentWrapper = styled.div`
	display: flex;
	align-items: center;
`;

export const OptionIcon = styled(Icon)`
	margin-right: ${spacingMap.xs};
`;